.App {
  height: 100vh;
  padding: 30px;
}

.sari-widget {
  width: 100%;
  height: 50vh;
}

//////////////////////
.d-flex {
  display: flex;
}

@for $i from 1 through 25 {
  .margin-right-#{$i} {
    margin-right: #{$i}px;
  }

  .margin-left-#{$i} {
    margin-left: #{$i}px;
  }

  .margin-top-#{$i} {
    margin-top: #{$i}px;
  }

  .margin-bottom-#{$i} {
    margin-bottom: #{$i}px;
  }

  .padding-right-#{$i} {
    padding-right: #{$i}px;
  }

  .padding-left-#{$i} {
    padding-left: #{$i}px;
  }

  .padding-top-#{$i} {
    padding-top: #{$i}px;
  }

  .padding-bottom-#{$i} {
    padding-bottom: #{$i}px;
  }
}